<template>
    <div class="home-root">

        <title-bar :title="'黑马彩票'"></title-bar>

        <div class="home-content">

            <swiper-pic></swiper-pic>

            <div v-for="history in histories" @click="clickItem(history)">
                <home-item :data="history"></home-item>
            </div>
        </div>
    </div>
</template>

<script>
    import TitleBar from "../../components/TitleBar";
    import SwiperPic from "../../components/SwiperPic";
    import HomeItem from "../../components/HomeItem";
    import NET from "../../config/net/NET";

    import {mapGetters, mapActions} from 'vuex'

    export default {
        name: "HomeTab",
        components: {HomeItem, SwiperPic, TitleBar},
        created() {
            this.$store.dispatch("loadHistory")
        },
        computed: {
            ...mapGetters({
                histories: 'historyList'
            }),
        },
        mounted() {
            this.selectTab(0)
            this.enableTab(true)
        },
        methods: {
            ...mapActions({
                selectTab: 'selectTab',
                enableTab: 'enableTab'
            }),
            clickItem(data) {
                this.$router.push({
                    name: "lotteryDetail",
                    params: {
                        data: data
                    }
                })
            }
        }
