<template>

    <div class="home-item">

        <div class="item-title">
            <span class="item-title-main">第{{data.code}}期</span>
            <span class="item-title-sub">{{date}} 21:20:20</span>
        </div>
        <div class="ball-container">

            <div class="ball" v-for="ball in redBalls">
                {{ball}}
            </div>
            <div class="ball blue">{{blueBall}}</div>

        </div>

        <div class="item-arrow">
            <icon name="angle-right" scale="2.4"></icon>
        </div>

    </div>

</template>

<script>
    import Icon from "vue-awesome/components/Icon";

    export default {
        name: "HomeItem",
        components: {Icon},
        props: {
            data: {
                required: true,
                type: Object
            }
        },
        computed: {
            redBalls() {
                return this.data.red.split(",")
            },
            blueBall() {
                return this.data.blue
            },
            date() {
                const date = this.data.date
                return date.substring(0,date.indexOf("("))
            }
        }
    }
</script>

<style scoped>

    .home-item {
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        width: 100%;
        display: flex;
        flex-direction: column;
        padding: 1rem;
        border: .1rem none #33333333;
        border-bottom-style: solid;
        justify-content: center;
        position: relative;
    }

    .home-item:active {
        background-color: #33333333;
        transition: all .4s;
    }

    .item-title {

    }

    .item-arrow {
        position: absolute;
        right: 4rem;
        color: #33333399
    }

    .item-title-main {
        font-size: 1.2rem;
    }

    .item-title-sub {
        font-size: 1rem;
        margin-left: .3rem;
    }

    .ball-container {
        margin-top: .8rem;
        display: flex;
    }

    .ball {
        width: 2.5rem;
        height: 2.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border: .12rem solid rgba(229, 28, 35, 1);
        border-radius: 1.5rem;
        margin-right: .8rem;
        color: rgba(229, 28, 35, 1);
    }

    .ball.blue {
        color: rgba(0, 0, 255, 1);
        border: .12rem solid rgba(0, 0, 255, 1);
    }

</style>